.container {
  width: 100%;
  background-color: black;
  color: white;
  position: relative;
}
.title {
  margin: auto;
  margin-top: 20px;
  padding: 10px;
  box-sizing: border-box;
  width: 50%;
  font-size: 40px;
  text-align: center;
  color: white;
  border: #a3f7ff 3px solid;
  border-radius: 20px;
  z-index: 1;
}
@media only screen and (max-width: 800px) {
  .title {
    width: 80%;
  }
}

.flexContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.content1 {
  background-image: url(../square-2724387_1920.jpg);
  background-attachment: scroll;
  background-size: cover;
  border-radius: 20px;
  border: 2px solid rgba(115, 229, 255);
  width: 300px;
  margin: 20px;
  color: rgb(68, 68, 68);
  text-shadow: 0px 0px 3px #ffffff;
  font-size: 20px;
  z-index: 1;
}
