.circle1 {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: rgb(247, 252, 183);
  opacity: 0.7;
  position: absolute;
  top: 20px;
  left: 80px;
  z-index: 0;
  animation: myOrbit 6s linear infinite;
}

@keyframes myOrbit {
  from {
    transform: rotate(0deg) translateX(150px) rotate(0deg);
  }
  to {
    transform: rotate(360deg) translateX(150px) rotate(-360deg);
  }
}
.circle2 {
  width: 200px;
  height: 200px;
  border-radius: 50%;
  background-color: rgb(248, 214, 237);
  opacity: 0.5;
  position: absolute;
  top: 160px;
  right: 300px;
  z-index: 0;
  animation: myOrbit2 8s linear infinite;
}

@keyframes myOrbit2 {
  from {
    transform: rotate(0deg) translateX(150px) rotate(0deg);
  }
  to {
    transform: rotate(-360deg) translateX(150px) rotate(360deg);
  }
}
