.container {
  position: relative;
  width: 100%;
  height: 400px;
  overflow-x: hidden;
  overflow-y: auto;
  perspective: 20px;
  perspective-origin: 0%;
  -webkit-overflow-scrolling: touch;
  z-index: 1;
}
.background {
  width: 100%;
  height: 800px;
  background: repeating-linear-gradient(
    90deg,
    #a3dfff,
    #a3dfff 20px,
    #ffffff 20px,
    #ffffff 40px
  );
}

