.background {
  width: 100%;
  height: 100vh;
  background-image: url(../hands-20333_1920.jpg);
  background-attachment: scroll;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: bottom;
  background-color: white;
}

.foreground {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  width: 100%;
  height: 100%;
}
.name {
  font-weight: 900;
  font-size: 80px;
  text-align: center;
  color: rgb(38, 59, 90);
  text-shadow: 2px 2px 10px white;
}
.description {
  font-weight: 800;
  font-size: 50px;
  text-align: center;
  color: rgb(50, 56, 65);
  text-shadow: 2px 2px 10px white;
}
.photo {
  width: 150px;
  height: auto;
  border-radius: 50%;
  border: 5px solid rgba(75, 80, 97, 0.479);
}
@media only screen and (max-width: 1200px) {
  .foreground {
    justify-content: center;
  }
}
@media only screen and (max-width: 800px) {
  .background {
    background-size: contain;
  }
  .name {
    font-size: 50px;
  }
  .description {
    font-size: 30px;
  }
}
