.container {
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: #03befc;
  width: 100%;
  color: white;
  z-index: 1;
  padding-bottom: 30px;
}
.title {
  font-size: 30px;
  font-weight: 800;
}
.flexContainer {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  justify-content: center;
}
.logoMail:hover {
  color: #b33e36;
}
.logoLinkedIn:hover {
  color: #005499;
}
.logoGitHub:hover {
  color: black;
}
a {
  color: white;
}
