.word {
  padding: 20px 80px;
  font-size: 18px;
  transition: transform 500ms;
}
.quote {
  padding: 20px 80px;
  font-size: 25px;
  transition: transform 500ms;
}
.word:hover,
.quote:hover {
  transform: scale(1.1);
}
.MuiPaper-elevation2 {
  background-color: #074775;
  color: white;
}
@media only screen and (max-width: 800px) {
  .word {
    padding: 20px 50px;
  }
  .quote {
    padding: 20px 50px;
  }
}
