.outerWrap {
  color: white;
  font-size: 25px;
  width: 100%;
  padding: 10px 20px;
  box-sizing: border-box;
}
.container {
  color: white;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 10px;
  box-sizing: border-box;
}
.title {
  margin: auto;
  margin-top: 20px;
  padding: 10px;
  box-sizing: border-box;
  width: 60%;
  font-size: 40px;
  text-align: center;
  color: #fff;
  border: #a3f7ff 3px solid;
  border-radius: 20px;
}
@media only screen and (max-width: 800px) {
  .title {
    width: 80%;
  }
}
